import React from 'react';
import ReactDOM from 'react-dom';
import 'bootstrap/dist/css/bootstrap.css';
import 'bootstrap/dist/css/bootstrap-theme.css';
import './common.css'

import registerServiceWorker from './registerServiceWorker';
import { BrowserRouter as Router, Route} from "react-router-dom";
import asyncComponent from './AsyncComponents'

const AsyncHome = asyncComponent(() => import("./pages/Home/Home"));
const AsyncChat = asyncComponent(() => import("./pages/Chat/Chat"));
const AsyncChatDoor = asyncComponent(() => import("./pages/ChatDoor/ChatDoor"));

const App = () => (
    <Router>
        <div className={'Apps'}>
            <Route exact path="/" component={AsyncHome} />
            <Route exact path="/door" component={AsyncChatDoor}/>
            <Route exact path="/chat" component={AsyncChat}/>
        </div>
    </Router>
);

ReactDOM.render(<App />, document.getElementById('root'));
registerServiceWorker();



